.Search {
  text-align: center;
  color: white;
  background-color: rgb(224, 96, 60);
  border: solid 2px rgb(255, 255, 255);
  border-radius: 10px;
  padding: 1%;
  margin: 3% auto;
  width: 300px;
  display: flex;
  flex-direction: column;
}

.Weather {
  color: white;
  background-color: rgb(224, 96, 60);
  text-align: center;
  margin: 3% auto;
  border: solid 2px white;
  padding: 2%;
  width: fit-content;
}

.err {
  color: red;
  text-align: center;
}

.desc {
  color: white;
  text-align: center;
}

button {
  width: 40%;
  margin: 8% auto 0;
  font-family: Roboto, monospace, Arial, Consolas;
}